@import "../config/config-gutter";

@each $key, $value in $gutter-size-map {
    @each $pm_key, $pm_value in $gutter-type-map {
        .#{$pm_key}-#{$key} {
            #{$pm_value}: $value; }
        .#{$pm_key}x-#{$key} {
            #{$pm_value}-left: $value;
            #{$pm_value}-right: $value; }
        .#{$pm_key}y-#{$key} {
            #{$pm_value}-top: $value;
            #{$pm_value}-bottom: $value; }


        @each $p, $k in $gutter-position-map {
            .#{$pm_key}#{$p}-#{$key} {
                #{$pm_value}-#{$k}: $value; }
            @if $key == left {
                .#{$pm_key}x-#{$key} {
                    #{$pm_value}-left: $value;
                    #{$pm_value}-right: $value; } }
            @if $key == top {
                .#{$pm_key}y-#{$key} {
                    #{$pm_value}-top: $value;
                    #{$pm_value}-bottom: $value; } } } } }


$screens_map: map-merge($screens, (nav: $nav-breakpoint));

@each $b_key, $b_value in  $screens_map {

    @include media-up($b_key) {
        @each $key, $value in $gutter-size-map {
            @each $pm_key, $pm_value in $gutter-type-map {
                .#{$b_key}-#{$pm_key}-#{$key} {
                    #{$pm_value}: $value; }
                .#{$b_key}-#{$pm_key}x-#{$key} {
                    #{$pm_value}-left: $value;
                    #{$pm_value}-right: $value; }
                .#{$b_key}-#{$pm_key}y-#{$key} {
                    #{$pm_value}-top: $value;
                    #{$pm_value}-bottom: $value; }

                @each $p, $k in $gutter-position-map {
                    .#{$b_key}-#{$pm_key}#{$p}-#{$key} {
                        #{$pm_value}-#{$k}: $value; }
                    @if $key == left {
                        .#{$b_key}-#{$pm_key}x-#{$key} {
                            #{$pm_value}-left: $value;
                            #{$pm_value}-right: $value; } }
                    @if $key == top {
                        .#{$b_key}-#{$pm_key}y-#{$key} {
                            #{$pm_value}-top: $value;
                            #{$pm_value}-bottom: $value; } } } } } } }

